body {
  background-color: #14131a;
}

.Prow1 {
  padding: 0px 30px;
}

.Prow1 h2 {
  text-decoration: underline rgb(104, 154, 230) 1px;
  text-underline-offset: 10px;
  margin-bottom: 20px;
  font-weight: 500;
  color: white;
  text-align: left;
  font-size: 29px;
}

.project-container {
  padding: 25px;
}

.project-content {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: space-around;
}

.project-card {
  flex: 1;
  background: #1c1b23;
  border: 1px solid rgb(56, 56, 56);
  color: white;
  border-radius: 10px;
  overflow-x: hidden;
  width: 23%;
  display: flex;
  flex-direction: column;
  max-width: 450px;
  min-width: 290px;
  padding: 10px;
}

.project-card img {
  width: 100%;
  max-height: 150px;
  min-height: 150px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  object-fit: fill;
}

.card-body {
  /* padding: 15px; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.card-title {
  font-weight: 500;
  font-size: 20px;
  margin: 15px 0px;
}

.card-text {
  font-weight: 300;
  text-align: left;
  line-height: 20px;
  font-size: 15px;
}

.btn {
  margin-top: 10px;
}
